import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQuery, VhQueryCafe } from 'ionic-vhframeworks';
import { ReceiptA4Component } from 'src/app/cafe/components/print/receipt/receipt-a4/receipt-a4.component';
import { ReceiptA5Component } from 'src/app/cafe/components/print/receipt/receipt-a5/receipt-a5.component';
import { ReceiptK80Col4Component } from 'src/app/cafe/components/print/receipt/receipt-k80-col4/receipt-k80-col4.component';
import { ReceiptK80Col3Component } from 'src/app/cafe/components/print/receipt/receipt-k80-col3/receipt-k80-col3.component';
import { ReceiptK57Col3Component } from 'src/app/cafe/components/print/receipt/receipt-k57-col3/receipt-k57-col3.component';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})


export class AddComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // biến lấy tên nhóm (form kg cho dùng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  value_sort: any;
  startDateTime: Date;
  endDateTime: Date;
  keySearch: any;
  type: any = '11'; // Biến lưu loại phiếu thu được chọn
  listPaymentBank: any = []; //danh sách ngân hàng thanh toán
  customer: any;
  money_currency: any;
  addReceiptForm: FormGroup;
  billInfo: any = { employee: this.vhAuth.getUser() };
  list_customers: any = [];
  nzOptions: any;
  receiptGroup: any[] = this.vhQueryCafe.getlocalIncoming_groups()
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private vhQuery: VhQuery,
    public languageService: LanguageService,
    private navCtrl: NavController,
    private fncService:FunctionService
  ) {
    this.getPaymentBankList();
    let options = [
      {
        value: '1',
        label: this.languageService.translate("Cash"),
        isLeaf: true
      },
      {
        value: '3',
        label: this.languageService.translate("Account"),
        children: this.listPaymentBank
      }
    ]
    this.nzOptions = options;
  }

  ngOnInit(): void {
    this.initForm();
    this.getCustomersList();
    let printer =  this.vhQueryCafe.getLocalPrintPage('page_desktop_receipt')
     
    if (printer ) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
  }
  checkPrinter: boolean = false;

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }

  initForm() {
    let id_group_default: any = this.receiptGroup.length ? this.receiptGroup.filter(item => item.default == true) : []
    let required: any = this.type == 7 ? Validators.compose([Validators.required]) : ""
    this.addReceiptForm = new FormGroup({
      date: new FormControl(new Date().toISOString()),
      customer: new FormControl('', Validators.compose([Validators.required])),
      total: new FormControl('', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl(''),
      payment_type: new FormControl('1'),
      wallet: new FormControl(null),
      type_receipt: new FormControl(this.type),
      id_group: new FormControl(id_group_default.length ? id_group_default[0]._id : null, required)
    });
  }
  
  /**
  * check validate hợp lệ
  * @param $event date
  */
   changeDate(event) {
    this.vhQueryCafe.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.addReceiptForm.controls['date'].setValue(new Date())
        }
      })
  }
  /**Hàm chọn loại phiếu thu*/
  chooseType(value) {
    this.initForm();
  }

  /**Get payment bank list */
  getPaymentBankList() {
    let listPayment = this.vhQueryCafe.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQueryCafe.getDefaultBranch()._id);
    this.listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
  }

  /**get customers list */
  getCustomersList() {
    this.list_customers = this.vhQueryCafe.getlocalCustomers().filter(customer => customer['_id'] != "id_retail" && customer['debt_enable'] == true);
  }

  /**Hàm trở lại trang trước */
  goBack(value?) {
    let data = { ...this.dataRestore }
    data.check = true
    this.router.navigate(['/cafe/dashboard/management/cashflow/receipts'], {
      state: value ? data : this.dataRestore
    });
  }

  /**Khởi tạo bill*/
  exportBill(formValue) {
    const bill: any = {
      bill_code: this.vhAlgorithm.createBillCode(new Date(formValue["date"])),
      date: new Date(formValue["date"]),
      id_employee: this.billInfo['employee']._id,
      id_branch: this.vhQueryCafe.getDefaultBranch()._id,
      bill_type: parseInt(this.type),
      content: formValue["content"],
      payment_type: formValue["payment_type"],
      tax: 0,
      total: parseFloat(this.money_currency.getRawValue()),
      payment: parseFloat(this.money_currency.getRawValue())
    }
    if (bill['payment_type'][0] == '3') {
      bill['id_wallet'] = bill['payment_type'][1];
      bill['payment_type'] = 3;
    } else {
      bill['payment_type'] = 1;
      bill['id_wallet'] = 'id_cash';
    }
    if (this.type == 11) {
      bill['id_customer'] = formValue['customer']
      this.billInfo['customer'] = this.vhQueryCafe.getlocalCustomer(formValue['customer'])
    }
    else {
      bill.name = formValue["customer"]
      bill.id_group = formValue["id_group"]
    }
    return bill;

  }

  /**Hàm tạo phiếu thu */
  addReceipt(print) {
    this.vhComponent.showLoading("").then(() => {
      const bill = this.exportBill(this.addReceiptForm['value'])
      this.vhQueryCafe
        .createBill( bill)
        .then((res: any) => {
          this.billInfo = { ...this.billInfo, ...bill, id: res._id }
          this.vhComponent.showToast(1500, (this.languageService.translate("Receipt has been created successfully")), "success-toast");
          this.vhComponent.hideLoading(0).then(() => {
            if (print) this.showPrint();
            else setTimeout(() => this.goBack(true), 200)
          })
        }, err => { })
    })
  }

  /**Print */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { billInfo: this.billInfo, printer: printer }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { this.goBack(true) })
          })
      }
    }, err => { this.goBack(true) })
  }

  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReceiptK57Col3Component
      case 'print_size_k80_3c': return ReceiptK80Col3Component
      case 'print_size_k80_4c': return ReceiptK80Col4Component
      case 'print_size_a5': return ReceiptA5Component
      case 'print_size_a4': return ReceiptA4Component
      default: return ReceiptA4Component
    }
  }

  /*----------Print------------------*/
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQueryCafe.getLocalPrintPage('page_desktop_receipt')
      if (printer  && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }


  /**Zorro modal save*/
  isVisible = false;
  isPrint: boolean;
  showModal(value): void {
    this.isPrint = value;
    this.isVisible = true;
  }
  handleOk(): void {
    this.isVisible = false;
    this.addReceipt(this.isPrint);
  }
  handleCancel(): void {
    this.isVisible = false;
  }
  /***********Thêm xóa sủa nhóm thu********** */
  isVisiblelist: boolean = false; // popup danh sách nhóm
  activeButton: boolean = false
  addGroup(nameGroup): void {
    let index = this.receiptGroup.findIndex(item => item.default)
    this.activeButton = true
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQueryCafe.addIncoming_group(value).then((res) => {
        this.receiptGroup = [...this.receiptGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Chưa nhập tên nhóm")
  }

  setDefault(item) {
    let index = this.receiptGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.receiptGroup[index].default = false;
      Promise.all([this.vhQueryCafe.updateIncoming_group(item._id, item), this.vhQueryCafe.updateIncoming_group(this.receiptGroup[index]._id, this.receiptGroup[index])])
        .then(() => { })
    } else {
      this.vhQueryCafe.updateIncoming_group(item._id, item).then(() => { })
    }
  }


  openLisGroup() {
    let id = this.receiptGroup.length ? this.receiptGroup.filter(item => item.default == true) : []
    this.isVisiblelist = true
    this.addReceiptForm['value'].id_group = id.length ? id[0]._id : null
  }
  handleCancellist() {
    this.isVisiblelist = false;
    this.changeEdit = ""
  }

  changeEdit: any = ''
  editGroup(_id) {
    this.changeEdit = _id
  }
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQueryCafe.updateIncoming_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }

  deleteGroup(_id, index) {
    this.vhQueryCafe.deleteIncoming_group(_id).then((bool) => {
      if (bool) {
        this.receiptGroup.splice(index, 1)
        this.initForm()
      }
      else this.vhComponent.alertMessageDesktop("error", "Không xóa được nhóm này")
    })
  }
}
